.top-copy {
  background-image: url("/bg.jpg");
  width: 100%;
  height: 100vh;
  background-size: cover;
  @include flex-column;
  color: $white;
  justify-content: space-evenly;
  @include breakpoint(lg){
    justify-content: center;
  }

  .copy-container {
    @include breakpoint(lg){
      margin-left: 5rem;
    }
    h1 {
      font-size: 3rem;
      text-align: center;
      margin-bottom: 1rem;
      @include breakpoint(lg){
        font-size: $h1;
        width: 50%;
        text-align: initial;
      }
    }
  
    p {
      @include font-family($font-jp, 300);
      font-size: $h5;
      text-align: center;
      @include breakpoint(lg){
        font-size: 1.5rem;
        text-align: initial;
      }
    }
  }
  button {  

    @include breakpoint(lg){
      align-self: flex-end;
      margin-right: 10rem;
    }
  }

}

.services {
  @include flex-column;
  background: $primary-color;
  padding: 5% 0;

  @include breakpoint(lg){
    padding: 5%;
  }


  h3 {
    color: $white;
  }

  .services-list {
    padding: 5% 0;
    margin: 3% 10%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: $white;
    box-shadow: 0px 5px 15px 0px $black10;
    border-radius: 10px;

    li {
      
      padding: 0.5rem;
      @include flex-column;
      width: 90%;
      margin-bottom: 2rem;
      @include breakpoint(lg){
        flex-direction: row;
        align-items: center;
        width: 35%;
        justify-content: space-between;

        &:first-child,
        &:nth-child(2) {
          margin-bottom: 3rem;
        }
      }

      .icon {
        color: $primary-color;
        opacity: 0.8;
        font-size: 3rem;
      }

      p {
        font-size: 0.9rem;
        margin: 0 1.5rem;
        @include font-family($font-jp, 300)
      }
    }
  }
}

.about {
  @include flex-column;
  justify-content: center;
  padding: 5% 0;
  @include breakpoint(lg) {
    padding: 5%;
  }
  div {
    padding: 2rem 0;
    h4 {
      text-align: center;
    }
    ul {
      
      @include breakpoint(lg) {
        min-width: 500px;
      }
      li {
        display: flex;
        align-items: center;
        margin: 1rem 0.5rem;

        @include breakpoint(lg) {
          margin: 1rem;
        } 
        h5 {
          color: $primary-color;
          font-size: $h5;
          text-align: right;
          width: 4rem;
          @include font-family($font-jp, 500)
        }
        p {
          padding-left: 1rem;
          margin-left: 1rem;
          border-left: 1px solid $primary-color;
          @include font-family($font-jp, 300);
          max-width: 240px;

          @include breakpoint(lg) {
            max-width: initial;
          }

        }
      }
    }  
  }
}

.contact {
  @include flex-column;
  justify-content: center;
  padding: 5%;
  background-color: $primary-color;
  color: $white;

  p {
    text-align: center;
    @include font-family($font-jp, 400);
  }
  #typeform {
    width: 50%; 
    height: 350px;
    padding: 2rem;
  }
}